
import { onMounted, ref, watch } from "vue";
import ApiService from "../../core/services/ApiService";
import { useI18n } from "vue-i18n";
import { useField } from "vee-validate";

export default {
  name: "ShipsSelect",
  props: {
    vessel_id: {
      type: String,
      default: "",
    },
    showLabel: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "small",
    },
    required: {
      type: Boolean,
      default: true,
    },
  },
  emits: ["update:vessel_id"],

  setup(props) {
    const { t } = useI18n();
    const inputShip = ref({
      loading: false,
      options: [],
      list: [],
    });

    const isShipRequired = (value) => {
      if (props.required && !value) {
        return t("rshipid");
      }
      return true;
    };

    const { errorMessage, value: element_id } = useField(
      "ship",
      isShipRequired
    );

    const getShip = () => {
      ApiService.get("/api/vessel").then(({ data }) => {
        inputShip.value.list = data.vessel;
        inputShip.value.options = data.vessel;
      });
    };
    const selectShip = (query) => {
      if (query !== "") {
        inputShip.value.loading = true;
        setTimeout(() => {
          inputShip.value.loading = false;
          inputShip.value.options = inputShip.value.list.filter(
            (item: Record<string, any>) => {
              return item.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
            }
          );
        }, 200);
      } else {
        inputShip.value.options = [];
      }
    };

    watch(
      () => props.vessel_id,
      (first) => {
        if (first) {
          element_id.value = first;
        }
      }
    );

    onMounted(() => getShip());

    return {
      element_id,
      inputShip,
      errorMessage,
      getShip,
      selectShip,
    };
  },
};
